:root {
  --text: #2B3044;
  --line: #BBC1E1;
  --line-active: #275EFE;
}

.setting_main{
  width: 95%;
  margin: 0 auto;
  height: 100vh;
  box-sizing: border-box;
  background: #000;
  position: relative;
  border: 20px solid #222;
  border-radius: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  .setting_header{
    display: flex;
    flex-direction: row;
    border: 1px solid red;
    background-color: #1C1C1E;
    padding: 3rem;
    width: 88.2%;
    margin: 0 auto;
    svg{
      margin-top: 2rem;
    }
    .setting_text{
      display: flex;
      flex-direction: column;
      padding: 1rem;
      margin-left: 1rem;


      h2{
        color: #D1C7C7;
        font-size: 5rem;
        b {
          display: inline-block;
          position: relative;
          text-decoration: none;
          color: inherit;
          margin: 0 var(--spacing, 0px);
          transition: margin .25s;
          svg {
              width: 15vw;
              height: 12vh;
              position: absolute;
              left: 50%;
              bottom: 0;
              transform: translate(-50%, 7px) translateZ(0);
              fill: none;
              stroke: var(--stroke, var(--line));
              stroke-linecap: round;
              stroke-width: 2px;
              stroke-dasharray: var(--offset, 69px) 278px;
              stroke-dashoffset: 361px;
              transition: stroke .25s ease var(--stroke-delay, 0s), stroke-dasharray .35s;
          }
          &:hover {
              --spacing: 4px;
              --stroke: var(--line-active);
              --stroke-delay: .1s;
              --offset: 180px;
          }
      }
      }
      p:first-of-type{
        color: #D1C7C7;
        font-size: 1rem;
        line-height: 1.5;
        margin-left: 1rem;
        margin-bottom: 1rem;
      }
      span{
        position: relative;
        border: 3px solid #D1C7C7;
        border-radius: 30px;
        margin-left: 1rem;

      }
      span:after{
        content: "";
        position: absolute;
        border: 3px solid #6DC9F7;
        border-radius: 30px;
        transform: translateY(-3px)translateX(-3px); animation: ani1 5s ease-in-out 0.5s  infinite;

      }
      @keyframes ani1 {
        0%{width: 1%;}
        100%{width: 100%;}
        }
      p:last-of-type{
        color: #D1C7C7;
        font-size: 3rem;
        margin: 1rem 0 0 1rem;
      }
    }
  }
  .setting_content{
    color: #D1C7C7;
    padding: 2rem;
    border: 1px solid blue;
    background-color: #1C1C1E;
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
    .setting_contents{
      display: flex;
      flex-direction: row;
      h3{
        font-size: 3rem;
        font-weight: bold;
      }
  
  
  $offset: 187;
  $duration: 1.4s;
  
  .spinner {
    animation: rotator 1.4s linear infinite;
    width: 40px;
    margin-left: 1rem;
  }
  
  @keyframes rotator {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(270deg); }
  }
  
  .path1 {
    stroke-dasharray: 187;
    stroke-dashoffset: 0;
    transform-origin: center;
    animation:
      dashes 1.4s ease-in-out infinite, 
      colors (1.4s*4) ease-in-out infinite;
  }
  
  @keyframes colors {
    0% { stroke: #4285F4; }
    25% { stroke: #fff; }
    50% { stroke: #4285F4; }
    75% { stroke: #fff; }
    100% { stroke: #4285F4; }
  }
  
  @keyframes dashes {
   0% { stroke-dashoffset: $offset; }
   50% {
     stroke-dashoffset: $offset/4;
     transform:rotate(135deg);
   }
   100% {
     stroke-dashoffset: $offset;
     transform:rotate(450deg);
   }
  }
    }
    .setting_index{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
     
      .setting_idx{
        border-radius: 20px 20px 0 0;
        background-image: linear-gradient(to bottom, #1C1C1E, rgba(79, 79, 79, 1));
        border-bottom: 5px solid;
        flex: 1;
        margin: 0 1rem;
        padding: 1rem;
        h4{
          font-size: 2rem;
          font-weight: bold;
          color: #fff;
          &:hover{
            color: #535353;
          }
        }
        p{
          padding: 0.3rem;
          font-size: 1.1rem;
          font-weight: 600;
          color: #535353;
          cursor: pointer;
          &:hover{
            color: #fff;
          }
        }
      }
    }


  }

}

@media (max-width: 768px) {
  .setting_main{
   
    .setting_header{
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-top: 3rem;
      padding: 2rem;
      svg{
        margin: 2rem auto;
        width: 50vw;
        
      }
      .setting_text{
        border: 1px solid red;
  
        h2{
          color: #D1C7C7;
          font-size: 3rem;
          margin-bottom: 1rem;
          b {
            position: relative;
            svg {
                width: 14vw;
                height: 12vh;
                position: absolute;
                left: 50%;
                top: -200%;
          
            }
            &:hover {
  
            }
        }
        }
        p:first-of-type{
          color: #D1C7C7;
          font-size: 0.7rem;
          margin-left: 1rem;
          margin-bottom: 1rem;
        }
        span{
          position: relative;
          border: 3px solid #D1C7C7;
          border-radius: 30px;
          margin-left: 1rem;
  
        }
        span:after{
          content: "";
          position: absolute;
          border: 3px solid #6DC9F7;
          border-radius: 30px;
          transform: translateY(-3px)translateX(-3px); animation: ani1 5s ease-in-out 0.5s  infinite;
  
        }
        @keyframes ani1 {
          0%{width: 1%;}
          100%{width: 100%;}
          }
        p:last-of-type{
          color: #D1C7C7;
          font-size: 3rem;
          margin: 1rem 0 0 1rem;
        }
      }
    }
  
  }
}