.kakao_main{
  width: 95%;
  height: 100vh;
  border: 1px solid red;
  box-sizing: border-box;
  position: relative;
  margin: 0 auto;
  .kakao_section{
    display: flex;
    flex-direction: row;
    position: absolute;
    height: 87vh;
    width: 85%;
    left: 50%;
    top: 52%;
    transform: translate(-50%, -50%);
    border: 10px solid #222;
    border-radius: 30px;
    background-color: #333;
    .kakao_movie{
      position: relative;
      top: 10%;
      left: 1%;
      width: 320px;
      height: 64vh;
      border: 10px solid #000;
      border-radius: 20px;

      .kakao_app{
        width: 300px;
      }

    }
    .kakao_container{
      display: flex;
      flex-direction: column;
      margin-top: 1rem;
      margin-left: 1rem;
      width: 70vw;
      height: 85vh;
      border: 1px solid red;
      .kakao_header{
        display: flex;
        flex-direction: column;
        padding: 1rem;
 
        .goo {
          position: relative;
          width: 100%;
          margin: 0 auto;
          filter: url(#goo);
          h1 {
            font-weight: bold;
            font-size: 6rem;
            color: rgba(250, 206, 0, 1);
            text-shadow: 1px 1px 0 #002d54;
          }  
          .drop {
            opacity: 0;
            width: 30px;
            height: 40px;
            background: rgba(250, 206, 0, 1);
            border-radius: 1000px;
            position: absolute;
            animation: drip 7s infinite;
            border: 2px solid #002d54;
            border-top: none;
            top: 0;
            left: 700px;
          }
          .drop:nth-child(2) {
            left: 300px;
            animation-delay: 3s;
          }
          .drop:nth-child(3) {
            left: 500px;
            animation-delay: 2s;
          }
          .drop:nth-child(4) {
            left: 700px;
            animation-delay: 4s;
          }
          .drop:nth-child(5) {
            left: 955px;
            animation-delay: 1.5s;
          }
          .drop:nth-child(6) {
            left: 150px;
            animation-delay: 5s;
          }
          
          @keyframes drip {
            0% {
              opacity: 0;
              transform: translateY(0);
            }
            35% {
              opacity: 1;
              transform: translateY(30px);
            }
            50% {
              transform: translateY(100vh);
            }
            100% {
              transform: translateY(100vh);
            }
          }
        }
        

        p{
          font-size: 1rem;
          color: #ABB2BF;
          font-weight: 550;
          margin-left: 1rem;
        }
      }
      .kakao_skill{
        margin-top: 1rem;
        margin-left: 2rem;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        box-sizing: border-box;
        span{
          border: 1px solid #ABB2BF;
          padding: 0.5rem;
          margin-right: 1rem;
          color: #ABB2BF;
          font-weight: 500;
          transition: .3s ease-in-out;
          cursor: pointer;
          box-sizing: border-box;
          &:hover{
            box-shadow: inset 12em 0 0 0 white;
            border: none;
            border-radius: 5px;
            box-sizing: border-box;
          }
        }
      }
      .kakao_date{
        margin: 3rem 2rem;
        span{
          border: 1px solid #ABB2BF;
          padding: 0.7rem;
          color: #ABB2BF;
        }
      }
      .kakao_overview{
        display: flex;
        flex-direction: column;
        margin-left: 2rem;
        h3{
          color: #ABB2BF;
          font-weight: bold;
          width: 5vw;

        }
        p{
          border: 1px solid red;
          padding: 2rem 1rem;
          color: #fff;
          width: 33vw;
          background-color: #222;
          border-radius: 20px;
          margin-top: 0.5rem;
        }
      }
      .kakao_description{
        display: flex;
        flex-direction: column;
        position: absolute;
        bottom: 1%;
        right: 2%;
        
        h3{
          color: #ABB2BF;
          font-weight: bold;
          padding-bottom: 0.5rem;
          width: 5vw;

        }
        .kakao_des{
          border-radius: 20px;
          width: 30vw;
          padding: 1rem;
          background-color: #036825;
          
          p{
            color: #fff;
            font-size: 1rem;
            font-weight: 400;
            line-height: 1.5;
          }
        }
      }
      .kakao_nav{
        display: flex;
        flex-direction: row;
        position: absolute;
        bottom: 5%;
        padding-left: 1rem;

        span{
          border: 1px solid #ABB2BF;
          padding: 0.5rem;
          margin-right: 1rem;
          color: #ABB2BF;
          font-weight: 500;
          cursor: pointer;
          transition: .3s ease-in-out;
          &:hover{
            box-shadow: inset 12em 12em 0 0 white;
            border: none;
            border-radius: 5px;
            box-sizing: border-box;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {

  .kakao_section{
    border: 1px solid blue !important;
    width: 90% !important;
    height: 91vh !important;
    top: 50% !important;
    .kakao_movie{
      background: url(../images/iphone.png)no-repeat 50% 50% / cover !important;
      width: 30% !important;
     } 
     .kakao_container{
      width: 70% !important;
      z-index: 1 !important;
      opacity: 0.7;
      
      .kakao_skill{
        span{
          margin-bottom: 1rem !important;
        }   
      }
      .kakao_overview{
        margin-top: 2rem !important;
        h3{
          font-size: 1.5rem !important;
           &:hover{
          width: 15vw !important;
        }
        }
       
        p{
          width: 40vw !important;
        }
      }
      .kakao_description{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      h3{
        font-size: 1.5rem !important;
        &:hover{
          width: 17vw !important;
        }
      }
      .kakao_des{
        width: 41vw !important;
        margin-bottom: 10rem !important;
      }
     }
     .goo{
      filter: none !important;
      h1{
        font-size: 2.5rem !important;
        color: #fff !important;
        margin-left: 1rem !important;
        margin-top: 3rem !important; 
      }
      .drop{
        display: none !important;
      }
     }
  }

}

